.button-component {
  text-transform: capitalize;
  border-radius: 6px !important;
  display: flex;
  align-items: center;
  justify-content: center;
  width: auto;
  padding: 0 20px;
  height: 48px;
  font-size: 16px;
  font-weight: 600;
  min-width: 90px !important;
  align-self: start;
  background-color: #092a4a;
  color: white;
}
.button-component span {
  font-size: 18px !important;
}
.button-component:hover {
  box-shadow: none;
}
.button-component:not(:disabled) {
  cursor: pointer;
}
.button-component:disabled {
  opacity: 0.7;
  cursor: no-drop;
}

.disabled {
  cursor: not-allowed;
}

.button-red {
  background: #ed1d24;
  color: #ffffff;
}

.button-gray {
  background: #545b62;
  color: white;
}

.button-yellow {
  background: #FDB813;
  color: #ffffff;
}

.button-primary-100 {
  background: #E3FCF1;
  color: #10975B;
}

.button-green {
  background-color: #10975B;
  color: #ffffff;
  box-shadow: 0px 8px 24px rgba(16, 151, 91, 0.25);
}
.button-green:hover {
  background-color: #0E8550;
}

.button-forestgreen {
  background-color: #02723a;
  color: #ffffff;
}

svg.anticon-spin path {
  color: #0f0f0f;
}

span.anticon.anticon-loading.ant-spin-dot {
  margin-left: 10px;
}

svg.anticon-spin {
  font-size: 15px;
}

@media screen and (min-width: 1366px) and (max-width: 1439px) {
  .button-component {
    padding: 0 6px;
    height: 35px;
    font-size: 10px;
    min-width: 70px !important;
  }
}